.color-card {
  background-color: #81b3d2; }

.color-card__title {
  font-size: 18px; }

.color-card__number,
.color-card__text {
  font-size: 14px; }

.color-card__text,
.color-card__title,
.color-card__number {
  text-transform: uppercase;
  text-align: center;
  color: #ffffff;
  margin: 0; }

.features__block {
  background: #eeeeee; }

.features__title {
  font-size: 18px; }

.features__number,
.features__text {
  font-size: 14px; }

.features__number,
.features__text,
.features__title {
  text-transform: uppercase;
  text-align: center;
  margin: 0; }

.site-list {
  padding: 0;
  margin: 0;
  position: relative; }

.site-list__item {
  display: flex;
  align-items: center;
  justify-content: center;
  list-style: none; }

.site-list__link {
  background-color: #81b3d2;
  color: #ffffff;
  font-weight: 700;
  font-size: 18px;
  text-transform: uppercase;
  text-decoration: none;
  padding: 15px 50px;
  box-shadow: 1px 1px 1px rgba(1, 1, 1, 0.2), inset 1px 1px 1px rgba(255, 255, 255, 0.2); }

.site-list__link:hover {
  background-color: #669ec0; }

.site-list__link:active {
  background-color: #5496bd; }

.site-list__link span:active {
  opacity: 0.3; }

.site-list__link:active:disabled {
  background-color: #e5e5e5;
  color: #ffffff; }

.link-size--a {
  padding: 15px 123px; }

.link-size--b {
  padding: 15px 97px; }

.link-size--c {
  padding: 15px 93px; }

.link-size--d {
  padding: 15px 97px 13px 97px; }

.item-logo {
  width: 320px;
  height: 55px;
  position: relative; }

.item-logo::after {
  content: "";
  background-repeat: no-repeat;
  background-position: 0 0;
  width: 102px;
  height: 84px;
  position: absolute;
  background-image: url("../../../img/menu/logotype-mobile.svg");
  top: 0%;
  left: 35%;
  z-index: 1; }

.main-nav__toggle {
  position: absolute;
  background-image: url("../../../img/menu/icon-menu.svg");
  top: 236px;
  right: 17px;
  width: 23px;
  height: 22px;
  background-color: transparent;
  border: 0;
  cursor: pointer; }

.intro {
  background-image: url("../img/header/back-mobile.jpg.jpg");
  background-repeat: no-repeat;
  width: 320px;
  height: 332px;
  position: relative; }

.intro__title {
  background-image: url("../../../img/header/text-index.svg");
  background-repeat: no-repeat;
  width: 320px;
  height: 332px;
  top: 27%;
  left: 5%;
  position: absolute; }

.description__title {
  text-transform: uppercase;
  font-size: 18px;
  text-align: center; }

.description__text {
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 400;
  text-align: center; }

.town {
  position: relative; }

.town__title {
  font-size: 18px;
  margin: 0; }

.town__text {
  font-size: 14px;
  margin: 0; }

.town__title,
.town__text {
  text-transform: uppercase; }

.town__housing,
.town__souvenirs,
.town__food {
  margin-left: 70px;
  margin-top: 30px; }

.town__housing::before {
  content: "";
  background-repeat: no-repeat;
  background-position: 0 0;
  background-image: url("../img/main/icon-house.svg");
  position: absolute;
  width: 40px;
  height: 40px;
  top: 53%;
  left: 7%; }

.town__souvenirs::before {
  content: "";
  background-repeat: no-repeat;
  background-position: 0 0;
  background-image: url("../img/main/icon-gift.svg");
  position: absolute;
  width: 40px;
  height: 40px;
  top: 71%;
  left: 7%; }

.town__food::before {
  content: "";
  background-repeat: no-repeat;
  background-position: 0 0;
  background-image: url("../img/main/icon-burger.svg");
  position: absolute;
  width: 40px;
  height: 40px;
  top: 86%;
  left: 6%; }

.interesting__title {
  font-size: 21px; }

.interesting__text {
  font-size: 14px; }

.interesting__title,
.interesting__text {
  text-transform: uppercase;
  text-align: center; }

.interesting__search {
  background-color: #81b3d2;
  font-size: 21px;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  padding: 17px 43px;
  display: block;
  margin: 0 auto;
  max-width: 186px; }

.interesting__search:hover {
  background-color: #669ec0; }

.interesting__search:active {
  background-color: #5496bd; }

.interesting__map {
  display: block;
  margin-top: 40px; }

.social__title {
  font-size: 21px;
  text-transform: uppercase;
  text-align: center; }

.social__item {
  background-color: #81b3d2;
  padding: 12px 24px;
  margin-right: 6px;
  position: relative; }

.social__item:first-child::before {
  position: absolute;
  content: "";
  background-position: 0 0;
  background-repeat: no-repeat;
  background-image: url("../img/footer/icon-twitter.svg");
  width: 16px;
  height: 15px;
  top: 36%;
  left: 30%; }

.social__item:nth-child(2)::before {
  position: absolute;
  content: "";
  background-position: 0 0;
  background-repeat: no-repeat;
  background-image: url("../img/footer/icon-facebook.svg");
  width: 16px;
  height: 15px;
  top: 36%;
  left: 41%; }

.social__item:last-child::before {
  position: absolute;
  content: "";
  background-position: 0 0;
  background-repeat: no-repeat;
  background-image: url("../img/footer/icon-youtube.svg");
  width: 24px;
  height: 15px;
  top: 36%;
  left: 30%; }

.social__item:first-child:hover {
  background-color: #669ec0; }

.social__item:first-child:active {
  background-color: #5496bd; }

.social__item:nth-child(2):hover {
  background-color: #669ec0; }

.social__item:nth-child(2):active {
  background-color: #5496bd; }

.social__item:last-child:hover {
  background-color: #669ec0; }

.social__item:last-child:active {
  background-color: #5496bd; }

.social__links {
  max-width: 162px;
  margin: 0 auto; }

.social__dev {
  font-size: 14px;
  text-transform: uppercase;
  display: block;
  margin: 0 auto;
  max-width: 121px;
  text-decoration: none;
  color: #000000;
  position: relative;
  margin-top: 30px;
  margin-bottom: 21px; }

.social__dev::after {
  content: "";
  background-repeat: no-repeat;
  background-position: 0 0;
  background-image: url("../img/footer/htmlacademy.svg");
  width: 30px;
  height: 35px;
  top: -16%;
  left: 84%;
  position: absolute; }
